.container_CreateEditChanel {
  background-color: rgba(0, 0, 0, 0.697);
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  display: flex;
}
.container2_CreateEditChanel {
  margin: auto;
  background-color: rgba(55, 57, 67, 0.293);
  display: flex;
  flex-direction: column;
  position: relative;
  width: 20rem;
  height: 30rem;
  padding: 0.8rem;
  border: 2px solid white;
  border-radius: 0.5rem;
  box-shadow: 2px 2px white, -2px 2px white;
}
.container2_CreateEditChanel h1 {
  text-decoration: underline;
  color: whitesmoke;
}
.container2_CreateEditChanel input,
.container2_CreateEditChanel textarea {
  color: aliceblue;
  resize: none;
  padding: 0.3rem;
  border: 2px solid rgb(137, 243, 250);
}
.ibtn,
.ibox {
  font-size: 1rem;
  padding: 0.2rem;
  background-color: transparent;
  color: black;
  border-radius: 0.2rem;
  margin: 0.2rem;
  font-weight: bold;
}
.ibtn {
  background-color: rgb(28, 28, 28);
  transition: 2s all;
}
.ibtn:hover {
  color: orange;
  background-color: black;
}
.ibtn_x {
  border: 0;
  padding: 0;
  color: white;
  background-color: transparent;
  font-size: 5rem;
  right: 50px;
  cursor: pointer;
  position: absolute;
  transition: 1s color;
  z-index: 1000;
}

.ibtn_x:hover {
  color: orange;
}

@media (max-width: 600px) {
  .container2_CreateEditChanel {
    width: 95vw;
    height: auto;
    min-height: 20rem;
    padding: 0.5rem;
  }
  .container2_CreateEditChanel h1 {
    font-size: 1.2rem;
  }
  .ibtn_x {
    font-size: 3rem;
    right: 10px;
  }
}

@media (max-width: 480px) {
  .container2_CreateEditChanel {
    width: 100vw;
    padding: 0.2rem;
    border-width: 1px;
  }
  .container2_CreateEditChanel h1 {
    font-size: 1rem;
  }
  .ibtn,
  .ibox {
    font-size: 0.9rem;
    padding: 0.1rem;
  }
}
